  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.1s transform ease;
  transition: 0.1s transform ease;
  border-radius: 50%; }

.edd-toggle input[type="checkbox"]:checked {
